.singlePage{
    margin-top: 47px;
    width: 100vw;
    height: 94.3vh;
    position: relative;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    &_background{
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
        filter: blur(4px);
    }
    &-details{
        border-radius: 10px;
        padding: 45px;
        background-color: rgba(255, 255, 255, 0.411);
        z-index: 1;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        position: relative;
        &_rate{
            position: absolute;
            top: 30px;
            left: 0;
            padding: 15px 15px 15px 25px;
            background-color: rgb(50, 66, 205);
            border-radius: 0px 3px 3px 0px;
            color: white;
            font-weight: bolder;
        }
        &_image{
            width: 450px;
            height: 600px;
            object-fit: cover;
            border-radius: 5px;
        }
        &-text{
            max-width: 600px;
            display: flex;
            flex-direction: column;
            margin-left: 120px;
            &_title{
                color: rgb(42, 42, 95);
                margin-bottom: 10px;
            }
            &_language{
                color:rgb(165, 25, 25);
                margin-bottom: 5px;
            }
            &_time{
                margin-bottom: 5px;
                color:rgb(5, 68, 44);
            }
            &_date{
                margin-bottom: 5px;
                color:rgb(114, 85, 5);
            }
            &_budget{
                color:rgb(34, 76, 192);
                margin-bottom: 5px;
            }
            &_overview{
                margin-bottom: 10px;
            }
            &-genres{
                width: 100%;
                display: flex;
                justify-content: flex-start;
                align-items: flex-start;
                flex-wrap: wrap;
                margin-bottom: 25px;
                &_item{
                    padding: 15px 25px;
                    background-color: rgba(0, 70, 64, 0.664);
                    color: whitesmoke;
                    margin-right: 5px;
                    margin-bottom: 5px;
                    border-radius: 5px;
                }
            }
            &_link{
                width: 100%;
                padding: 15px;
                background-color: rgb(7, 7, 128);
                color: white;
                font-weight: bold;
                text-align: center;
                border-radius: 3px;
                transition: all ease 1s;
                margin-bottom: 20px;
                &:hover{
                    transition: all ease 1s;
                    background-color: rgba(7, 7, 128, 0.342);
                    color: rgb(0, 0, 53);
                }
            }
        }
    }
}

.actors{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    overflow-x: scroll;
    overflow-y: hidden;
    max-height: 220px;
    width: 100%;
    &-card{
        max-width: 150px;
        height: 220px;
        margin-right: 10px;
        img{
            width: 100px;
            height: 150px;
            object-fit: cover;
        }
    }
}