.movieCard{
    max-width: 300px;
    border: 3px solid rgb(60, 60, 250);
    max-height: 620px;
    margin-bottom: 50px;
    position: relative;
    border-radius: 5px;
    box-shadow: 1px 3px 14px rgb(75, 75, 75);
    background-color: #032541;
    span{
        position: absolute;
        top: 25px;
        right: 0px;
        background-color: rgb(0, 83, 151);
        padding: 10px 15px 10px 10px;
        border-radius: 3px 0px 0px 3px;
        color: white;
    }
    img{
        object-fit: cover;
        width: 100%;
        height: 370px;
        border-radius: 5px 5px 0px 0px;
    }
    &-text{
        width: 100%;
        padding: 10px 25px;
        margin-top: 15px;
        h3{
            font-size: 32px;
            color: #fff;
            margin-bottom: 12px;  
            white-space: wrap; 
            width: 100%;
            height: 75px; 
            overflow: hidden;
            text-overflow: ellipsis;
        }
        p{
            color: silver;
            margin-bottom: 12px;
        }
        a{
            text-decoration: none;
            display: inline-block;
            padding: 15px 20px;
            background-color: rgb(43, 141, 187);
            border-radius: 3px;
            color: white;
            font-weight: bold;
            width: 100%;
            text-align: center;
            margin-bottom: 12px;
        }
    }
}